.help {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 300px;
}

.help__image {
  width: 100%;
  height: 100%;
}

.help__text-container {
  position: absolute;
  /* top: 34%; */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.help__title {
  width: 100%;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

.help__text {
  font-family: "Odnoklassniki";
  font-size: 10px;
  color: white;
  text-align: center;
  margin: 4px 0;
}

.help__link {
  text-decoration: underline;
}



@media (max-width: 3000px) {
  .player__text {
    font-size: 66px;
  }
  .help__text {
    font-size: 10px;
  }
}

@media (max-width: 2000px) {
  .help {
    height: 280px;
  }
}

@media (max-width: 1800px) {
  .help {
    height: 240px;
  }

  .player__text {
