.page-navigation {
  width: 100%;
  height: 300px;
  margin: auto;
  margin-bottom: 0;
  padding: 16px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0f0f0;
  box-sizing: border-box;
}

.page-navigation__container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 24px;
  transform: translateY(12px);
}

.page-navigation__link {
  width: 100%;
  /* width: 48%;
  margin: 10px auto; */
}

.page-navigation__next {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  /* height: 148px; */
  box-sizing: border-box;
  padding: 0 8px 16px;
  background-color: white;
  box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
}

.page-navigation__prev {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  /* height: 148px; */
  box-sizing: border-box;
  padding: 0 8px 16px;
  background-color: white;
  box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
}

.page-navigation__image {
  position: relative;
  /* top: -25%; */
  width: 116px;
  margin: -40px 0 20px;
}

.page-navigation__title {
  padding-bottom: 2px;
  font-size: 16px;
  line-height: 23px;
  font-weight: 700;
  font-family: "Odnoklassniki";
  text-align: center;
}

.page-navigation__location {
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  font-family: "Odnoklassniki";
  text-align: center;
  color: #666666;
}

@media (max-width: 2000px) {
  .page-navigation {
    height: 280px;
  }
}

@media (max-width: 1800px) {
  .page-navigation {
    height: 240px;
  }

  .page-navigation__title {
    font-size: 18px;
  }

  .page-navigation__image {
    width: 106px;
    margin: -40px 0 10px;
  }
}

@media (max-width: 1600px) {
  .page-navigation {
    height: 200px;
    padding: 12px 24px;
  }

  .page-navigation__container {
    transform: translateY(20px);
  }

  .page-navigation__image {
    width: 80px;
    margin: -40px 0 4px;
  }

  .page-navigation__title {
    font-size: 16px;
  }
}

@media (max-width: 1400px) {
  .page-navigation {
    height: 180px;
    padding: 10px 20px;
  }

  .page-navigation__container {
    transform: translateY(16px);
  }

  .page-navigation__image {
    width: 70px;
    margin: -30px 0 4px;
  }

  .page-navigation__title {
    font-size: 16px;
  }
}

@media (max-width: 1200px) {
  .page-navigation__title {
    font-size: 14px;
  }

  .page-navigation__next,
  .page-navigation__prev {
    padding-bottom: 0 4px 4px;
  }

  .page-navigation__image {
    width: 60px;
  }

  .page-navigation__location {
    font-size: 12px;
  }
}

@media (max-width: 1000px) {
  .page-navigation {
    height: 160px;
  }

  .page-navigation__container {
    transform: translateY(10px);
  }
}

@media (max-width: 800px) {

  .page-navigation {
    flex-direction: column;
    height: auto;
    padding: 24px;
  }

  .page-navigation__container {
    flex-direction: column;
    align-items: center;
    gap: 40px;
  }

  .page-navigation__link {
    width: 100%;
    max-width: 300px;
  }

  .page-navigation__title {
    font-size: 14px;
  }

  .page-navigation__location {
    font-size: 12px;
  }

  .page-navigation__next,
  .page-navigation__prev {
    height: 108px;
    padding-bottom: 30px;
  }

  .page-navigation__image {
    margin: 0;
  }
}

@media (max-width: 600px) {

  .page-navigation {
    margin: auto 0 0 -30px;
    padding: 40px 0;
    width: calc(100% + 60px);
    gap: 46px;
  }

  .page-navigation__container {
    gap: 52px;
  }

  .page-navigation__image {
    width: 80px;
    margin-bottom: 8px;
  }

  .page-navigation__title {
    line-height: 100%;
  }
}

@media (max-width: 600px) {
  .page-navigation {
    padding: 40px 0 50px;
  }

  .page-navigation__image {
    width: 60px;
  }
}